body{
  background-color: rgb(99, 216, 128);
}

.app{
  background-color: rgb(24, 102, 95);
  min-height:500px;
  width:400px;
  margin:40px auto;
  padding-bottom: 20px;
  border-radius: 5px;
}

.form input{
  background-color: rgb(232, 255, 185);
  border:0;
  height:40px;
  width:240px;
  padding:10px;
  margin:20px;
  border-radius: 5px;
  color:rgb(236, 39, 105);
  font-size: 20px;
  border:none;
  font-weight: bold;
}

.form input::placeholder{
  color:rgb(236, 39, 105);
  opacity: 0.6;
  border:none;
}

.form input:focus{
  outline:none;
}

.form button{
  padding:18px;
  border-radius: 5px;
  background-color: rgb(110, 163, 114);
  color:white;
  font-size: larger;
  border: none;
  cursor: pointer;
}

.form button:focus{
  outline:none;
}

.list {
  background-color: rgb(236, 39, 105);
  margin:20px;
  border-radius:5px;
}

.list p {
  font-size: 20px;
  color:white;
  word-wrap: break-word;
  position: relative;
}

.list input {
  width:85%;
  background: transparent;
  border:none;
  padding:15px;
  color:white;
}

.list input:focus{
  outline:none;
}

.icon {
  position: absolute;
  padding: 10px 5px 5px 5px;
  right:5px;
  cursor: pointer;
}

@media only screen and (max-width:600px){
  .app {
    width:95%;
    margin:0 auto;
  }

  .form input {
    width:70%;
  }
}